<template>
  <div class="help">
    <h2>常见问题解答</h2>
    <div class="item">
      <h3>1、 支付后,没有收到动态密码怎么办?</h3>
      <div class="line_h">答:动态密码不是通过短信发送,支付成功后,点击微信支付成功页面下方的完成，会自动跳转到密码页面,如已关闭支付成功页面,可再次扫码,点继续使用,可重新获取动态密码。</div>
    </div>
    <div class="item">
      <h3>2、为什么 正确输入了密码,还是不能充电?</h3>
      <div class="line_h">答:正确输入密码后, 开机或电源指示灯处会常亮绿灯,同时会亮起蓝色电量指示灯。如没有亮起上述指示灯,则有可能是充电器本身已损坏。</div>
    </div>
    <div class="item">
      <h3>3、我充了一会电,中途拔掉了没充,想继续再充电时为何充不了?</h3>
      <div class="line_h">答:再次扫码(无需再次充值)选择继续使用,重新输入新的动态密码即可继续充电。</div>
    </div>
    <div class="item">
      <h3>4、我扫描充电器上的二维码,提示充电器没有上架,这是什么原因?</h3>
      <div class="line_h">答:出现这种提示,是产品设备还没有激活,请联系消费场所负责人协助激活设备后才可使用。</div>
    </div>
  </div>
</template>

<script>
import { kefu, gethelp } from '@/axios/getData'
import { Icon, Popup, Dialog } from 'vant';
// import url from "@/axios/api.js";
// import axios from "axios";
export default {
  data(){
    return{
      data:[]
    }
  },
  components: {
    'van-icon': Icon,
    "van-popup": Popup
  },
  created() {
    this.getdata();
  },
  methods:{
    async getdata(){
      var res = await gethelp();
      this.data = res.data.article_list
      // axios.post( url.help,data ).then( res => {
      //   this.data = res.data.data.article_list
      //   console.log( this.data )
      //   // list.forEach(item => {
      //   //   this.province_list.push(item.name)
      //   //   this.province_data.push(item.id)
      //   // });
      // })
    },
    //  联系客服
    kefu(){
      var that = this;
      if(that.$store.state.kefu){
        Dialog.confirm({
          title: that.$store.state.kefu.title,
          message: that.$store.state.kefu.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
        return;
      }
      kefu().then(res =>{
        that.$store.state.kefu = res.data
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
      })
    },
  },
}
